.profile-container {
    display: flex;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    max-width: 100%;
    background-color: #fff;
}

.profile-container > div {
    display: flex;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.profile-container > div:first-child > h1 {
    color: rgba(0,0,0,0.555);
    font: 500 2.2vmax 'Roboto';
    transform: translate(-10vmax, -2vmax);
}

.profile-container > div:first-child > img {
    width: 20vmax;
    height: 20vmax;  /* i added */
    border-radius: 100%;
    transition: all .5s;
}

.profile-container > div:first-child > img:hover {
    transform: scale(1.05);
}

.profile-container > div:first-child > a {
    border: none;
    background-color: tomato;
    font: 400 1vmax 'Roboto';
    color: #fff;
    text-decoration: none;
    padding: 0.5vmax;
    width: 30%;
    margin: 4vmax;
    text-align: center;
    transition: all .5s;
}

.profile-container > div:first-child > a:hover {
    background-color: rgb(204, 78, 56);
}

.profile-container > div:last-child {
    justify-content: space-evenly;
    padding: 5vmax;
    box-sizing: border-box;
    align-items: flex-start;
}

.profile-container > div:last-child > div > h4 {
    color: #000;
    font: 400 1.2vmax 'Roboto';
}

.profile-container > div:last-child > div > p {
    color: rgb(0,0,0,0.484);
    font: 400 1vmax cursive;
    margin: 0.2vmax;
}

.profile-container > div:last-child > div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
}

.profile-container > div:last-child > div:last-child > a {
    border: none;
    background-color: rgb(68,68,68);
    font: 400 1vmax 'Roboto';
    color: #fff;
    text-decoration: none;
    padding: 0.5vmax;
    text-align: center;
    transition: all .5s;
    margin: 1vmax 0;
}

.profile-container > div:last-child > div:last-child > a:hover {
    background-color: rgb(31,31,31);
}




@media screen and (max-width: 600px) {
    .profile-container {
        flex-direction: column;
    }

    .profile-container > div:first-child > h1 {
        font: 400 1.7vmax 'Roboto';
        transform: translateY(-2vmax);
    }
    
    .profile-container > div:first-child > a {
        font: 400 1.7vmax 'Roboto';
        padding: 1vmax;
    }
    
    .profile-container > div:last-child {
        text-align: center;
        align-items: center;
    }
    
    .profile-container > div:last-child > div > h4 {
        font: 400 2.5vmax 'Roboto';
    }
    
    .profile-container > div:last-child > div > p {
        font: 400 2vmax cursive;
        margin: 0.5vmax 0;
    }
    
    .profile-container > div:last-child > div:last-child > a {
        font: 400 1.8vmax 'Roboto';
        margin: 2vmax 0;
        padding: 1vmax;
    }
}