.create-product-container {
    width: 100%;
    height: 100vh;
    box-sizing: border-box;
    background-color: rgb(221,221,221);
    border-left: 1px solid rgba(0,0,0,0.158);

    display: flex;
    flex-direction: column;
}

.create-product-container h1 {
    color: rgba(0,0,0,0.733);
    font: 300 2rem 'Roboto';
    text-align: center;
}

.createProductForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    margin: auto;
    padding: 3vmax;
    height: 70%;
    width: 40vh;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0,0,0,0.267);
}

.createProductForm > div {
    display: flex;
    align-items: center;
    width: 100%;
}
.createProductForm > div > input,
.createProductForm > div > select,
.createProductForm > div > textarea {
    padding: 1vmax 1vmax 1vmax 4vmax;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    font: 300 .9vmax cursive;
    outline: none;
}

.createProductForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623)
}

#createProductFormFile > img {
    width: 3vmax;
    border-radius: 100%;
}

#createProductFormFile > input {
    display: flex;
    padding: 0;
}

#createProductFormFile > input::file-selector-button {
    cursor: pointer;
    width: 100%;
    height: 5vh;
    z-index: 2;
    border: none;
    padding: 0 1vmax;
    font: 400 0.8vmax cursive;
    transition: all .5s;
    color: rgba(0,0,0,0.623);
    background-color: #fff;
}
#createProductFormFile > input::file-selector-button:hover {
    background-color: rgb(231,231,231);
}

#createProductFormImage {
    width: 100%;
    overflow: auto;
}

#createProductFormImage > img {
    width: 3vmax;
    margin: 0 0.5vmax;
}

#createProductButton {
    border: none;
    background-color: tomato;
    color: #fff;
    width: 100%;
    font: 300 .9vmax 'Roboto';
    padding: 0.8vmax;
    cursor: pointer;
    transition: all .5s;
    border-radius: 4px;
    outline: none;
    box-shadow: 0 2px 5px rgba(0,0,0,0.219);
}

#createProductButton:hover {
    background-color: rgba(179, 66, 46);
}


@media screen and (max-width: 600px) {

    .create-product-container {
        background-color: #fff;
    }

    .createProductForm {
        padding: 5vmax;
    }

    .createProductForm > div > input,
    .createProductForm > div > select,
    .createProductForm > div > textarea {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax cursive;
    }

    .createProductForm > div > svg {
        font-size: 2.8vmax;
    }

    #createProductFormImage > img {
        width: 8vmax;
        /* border-radius: 100%; */
    }

    #createProductFormImage > input::file-selector-button {
        height: 7vh;
        font: 400 1.8vmax cursive;
    }

    #createProductButton {
        font: 300 1.9vmax 'Roboto';
        padding: 1.8vmax;
    }
}