
.updateOrderForm {
    margin: 5vmax 0;
    padding: 3vmax;
    background-color: #fff;
}

.updateOrderForm > div {
    display: flex;
    align-items: center;
    width: 100%;
}
.updateOrderForm > div > select {
    padding: 1vmax 4vmax;
    width: 100%;
    margin: 2rem 0;
    box-sizing: border-box;
    border: 1px solid rgba(0,0,0,0.267);
    border-radius: 4px;
    font: 300 .9vmax cursive;
    outline: none;
}

.updateOrderForm > div > svg {
    position: absolute;
    transform: translateX(1vmax);
    font-size: 1.6vmax;
    color: rgba(0,0,0,0.623)
}


@media screen and (max-width: 600px) {

    .updateOrderForm {
        padding: 5vmax;
    }

    .updateOrderForm > div > select {
        padding: 2.5vmax 5vmax;
        font: 300 1.7vmax cursive;
    }

    .updateOrderForm > div > svg {
        font-size: 2.8vmax;
    }
}