.page-not-found {
    margin: auto;
    text-align: center;
    padding: 10vmax;
    height: 50vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.page-not-found > svg {
    font-size: 7vmax;
    color: tomato;
}

.page-not-found > p {
    font-size: 2vmax;
}

.page-not-found > a {
    background-color: rgb(51,51,51);
    color: white;
    border: none;
    padding: 1vmax 3vmax;
    cursor: pointer;
    font: 400 1vmax 'Roboto';
    text-decoration: none;
    margin: 2vmax;
}

@media screen and (max-width: 600px) {
    .page-not-found > a {
        padding: 3vw 6vw;
        font: 400 4vw 'Roboto';
        margin: 2vmax;
    }

    .page-not-found > svg {
        font-size: 20vw;
    }

    .page-not-found > p {
        margin: 2vmax;
        font-size: 5vw;
    }
}